* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'open sans', sans-serif;
  font-size: 20px;
}


* {
  margin: 0;
  padding: 0;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  color: #222;
}

header {
  -webkit-flex-basis: 30vh;
          flex-basis: 30vh;
  position: relative;
  overflow: hidden;
}

section.offer {
  position: relative;
  height: 10vh;
  background-color: #eee;
  overflow: hidden;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 30px 0;
}

aside {
  min-width: 240px;
}

section.page {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

section.page .home {
  margin-bottom: 30px;
}

footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
}
header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
nav.main {
    padding: 0 30px 30px 0;
}

nav.main ul {
    list-style-type: none;
}

nav.main a {
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    -webkit-transition: .2s;
    transition: .2s;
}

nav.main li a:hover {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}

nav.main li a.active {
    border-left: 10px gray solid;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
    border-left-color: #d33d77;
}

nav.main li:nth-child(2) a.active {
    border-left-color: #5050ce;
}

nav.main li:nth-child(3) a.active {
    border-left-color: #ffe601;
}

nav.main button {
    position: relative;
    margin-top: 20px;
    padding: 5px 20px;
    border-style: none;
    background-color: #eee;
    font-family: 'open sans', sans-serif;
    font-size: 22px;
    overflow: hidden;
}

nav.main button::before {
    content: "";
    position: absolute;
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 3px;
    background-color: #000;
    -webkit-transition: .7s cubic-bezier(0, 1.8, 1, 1);
    transition: .7s cubic-bezier(0, 1.8, 1, 1);
}

nav.main button:hover::before {
    right: 0;
}
.products,
.product {
  margin-top: 20px;
}

.products h2,
.product h2 {
  margin-bottom: 20px;
}

.products ul {
  list-style: square;
  padding-left: 30px;
}

.products li {
  margin-bottom: 20px;
}

.product div {
  font-size: 24px;
  margin: 10px 0 20px;
}

.products a,
.product a {
  text-decoration: none;
  color: #333;
}

.products a:hover,
.product a:hover {
  text-decoration: underline;
  color: #d33d77;
}
.contact {
    margin-top: 20px;
}

.contact textarea {
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.6;
    font-size: 16px;
    padding-left: 5px;
}

.contact button {
    display: block;
    margin: 0 20% 0 auto;
    padding: 10px 20px;
    border: 2px solid black;
    background-color: #fff;
    cursor: pointer;
    outline: none;
    font-size: 20px;
    -webkit-transition: .2s;
    transition: .2s;
}

.contact button:hover {
    color: #ffe601;
    background-color: #000;
}
.login {
    margin-top: 20px;
}

.login label {
    font-weight: bold;
}

.login input {
    display: block;
    height: 40px;
    padding-left: 5px;
    font-size: 16px;
}

.login button {
    display: block;
    margin-top: 25px;
    padding: 10px 20px;
    border: 2px solid black;
    background-color: #fff;
    cursor: pointer;
    outline: none;
    font-size: 20px;
    -webkit-transition: .2s;
    transition: .2s;
}

.login button:hover {
    color: #ffe601;
    background-color: #000;
}
section.offer h1 {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    -webkit-transform: translate(100%, -50%);
            transform: translate(100%, -50%);
    text-align: center;
    -webkit-animation: text 15s linear infinite;
            animation: text 15s linear infinite;
}

section.offer h1:nth-child(2) {
    -webkit-animation-delay: 5s;
            animation-delay: 5s;
}

section.offer h1:nth-child(3) {
    -webkit-animation-delay: 10s;
            animation-delay: 10s;
}

@-webkit-keyframes text {
    0% {
        -webkit-transform: translate(100%, -50%);
                transform: translate(100%, -50%)
    }

    3% {
        -webkit-transform: translate(100%, -50%);
                transform: translate(100%, -50%)
    }

    6% {
        -webkit-transform: translate(0, -50%);
                transform: translate(0, -50%);
    }

    30% {
        -webkit-transform: translate(0, -50%);
                transform: translate(0, -50%);
    }

    33% {
        -webkit-transform: translate(-100%, -50%);
                transform: translate(-100%, -50%);
    }

    100% {
        -webkit-transform: translate(-100%, -50%);
                transform: translate(-100%, -50%);
    }
}

@keyframes text {
    0% {
        -webkit-transform: translate(100%, -50%);
                transform: translate(100%, -50%)
    }

    3% {
        -webkit-transform: translate(100%, -50%);
                transform: translate(100%, -50%)
    }

    6% {
        -webkit-transform: translate(0, -50%);
                transform: translate(0, -50%);
    }

    30% {
        -webkit-transform: translate(0, -50%);
                transform: translate(0, -50%);
    }

    33% {
        -webkit-transform: translate(-100%, -50%);
                transform: translate(-100%, -50%);
    }

    100% {
        -webkit-transform: translate(-100%, -50%);
                transform: translate(-100%, -50%);
    }
}
